<template>
  <v-footer app height="36px" class="pa-0">
    <v-layout row class="d-flex">
      <v-flex xs3 class="text-left">
        <v-btn text @click="switchThemeMethod()">Switch Theme</v-btn>
      </v-flex>
      <v-flex xs6 class="mt-2 d-xs-block d-sm-none">
        &copy;2019 - 2020 — <strong>Tind</strong>
      </v-flex>
      <v-flex xs6 class="mt-2 d-none d-sm-block">
        &copy;2019 - 2020 — <strong>Tind</strong> - build on VueJS, FireStore, Vuetify
      </v-flex>
      <v-flex xs3 class="text-right">
        <div v-show="false">{{this.$vuetify.breakpoint.name}}</div>
        <v-btn v-show="loggedIn" text @click="print()">Print</v-btn>
      </v-flex>
    </v-layout>
  </v-footer>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'my-footer',
  props: ['breakpointName', 'breakpointHeight', 'breakpointWidth'],
  methods: {
    ...mapActions('global', {
      switchTheme: 'changeTheme',
    }),
    switchThemeMethod () {
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
        this.switchTheme()
    },
    print () {
      window.print();
      return false;
    },
  },
  computed: {
    ...mapGetters({
      loggedIn: 'user/loggedIn',
    })
  }
}
</script>
