<template>
  <v-app>
    <v-content class="zindex d-flex justify-center">
      <v-row>
        <v-slide-y-transition mode="out-in">
              <router-view></router-view>
        </v-slide-y-transition>
      </v-row>
      <my-footer :breakpointName="$vuetify.breakpoint.name" :breakpointWidth="$vuetify.breakpoint.width" :breakpointHeight="$vuetify.breakpoint.height"></my-footer>
    </v-content>
  </v-app>
</template>

<script>
import { mapGetters } from 'vuex'
import MyFooter from './components/MyFooter'
export default {
  name: 'app',
  components: { MyFooter },
  computed: {
    breakpointName () {
		return this.$vuetify.breakpoint.name
    },
    breakpointWidth () {
      return this.$vuetify.breakpoint.width
    },
    breakpointHeight () {
      return this.$vuetify.breakpoint.height
    },
    ...mapGetters({
      theme: 'global/theme',
      loggedIn: 'user/loggedIn'
    })
  },
  created: function() {
    this.$vuetify.theme.dark = this.theme === 'dark' ? true : false;
  }
}
</script>

<style>
body {
  margin: 0;
}

main {
  text-align: center;
  margin-top: 0px;
}

.zindex {
  z-index: 999;
}


</style>
